"use client";
import { useEffect, useState } from "react";
import { Heading, Text } from "@radix-ui/themes";
import Image from "next/image";
import { Navbar } from "@/components/modules/Navbar";

export default function Page() {
  const [user, setUser] = useState<string | null>(null);
  const [pullUps, setPullUps] = useState<string | null>(null);
  const [hangingPullUps, setHangingPullUps] = useState<string | null>(null);
  const [pushUps, setPushUps] = useState<string | null>(null);
  const [abs, setAbs] = useState<string | null>(null);
  const [longJump, setLongJump] = useState<string | null>(null);
  const [forwardBend, setForwardBend] = useState<string | null>(null);

  useEffect(() => {
    setUser(localStorage.getItem("user"));
    setPullUps(localStorage.getItem("Mark_Pull-ups"));
    setHangingPullUps(localStorage.getItem("Mark_HangingPull-ups"));
    setPushUps(localStorage.getItem("Mark_Push-ups"));
    setAbs(localStorage.getItem("Mark_ABS"));
    setLongJump(localStorage.getItem("Mark_longJump"));
    setForwardBend(localStorage.getItem("Mark_ForwardBend"));
  }, []);

  const getGradeColor = (grade: string | null) => {
    switch (grade) {
      case "5":
        return "text-green-600";
      case "4":
        return "text-green-500";
      case "3":
        return "text-yellow-600";
      case "2":
        return "text-red-500";
      default:
        return "";
    }
  };

  const renderResultBlock = (label: string, value: string | null) => {
    if (!value) return null;
    return (
      <div className="bg-[#F3F7F9] rounded-xl flex w-full justify-between items-center px-4 py-3">
        <p>{label}</p>
        <p className={getGradeColor(value)}>{value}</p>
      </div>
    );
  };

  return (
    <>
      <Navbar type="Main" />
      <br />
      <div className="flex flex-col gap-4">
        <div className="flex gap-2 items-center">
          <Heading>{user}</Heading>
          <Image
            src={"/Crown.svg"}
            width={16}
            height={16}
            alt="Crown"
            className="mt-1"
          />
        </div>
        <div className="bg-[#F3F7F9] rounded-xl flex w-full justify-between items-center px-4 py-3">
          <p>circle</p>
          <p>Средний рейтинг</p>
        </div>
        <div className="flex flex-col gap-3">
          <Text weight={"medium"}>Результаты:</Text>
          <div className="flex flex-col gap-2">
            {renderResultBlock(
              "Подтягивания из виса лежа на высокой перекладине",
              pullUps
            )}
            {renderResultBlock("Подтягивания в висе", hangingPullUps)}
            {renderResultBlock("Отжимания", pushUps)}
            {renderResultBlock("Пресс", abs)}
            {renderResultBlock("Прыжок в длину с места", longJump)}
            {renderResultBlock("Наклон вперед", forwardBend)}
          </div>
        </div>
      </div>
    </>
  );
}
